/*
*
* headers
* --------------------------------------------------
*/

// header-absolute
// -------------------------------------------------

.desktop{
  .header-absolute{
    @media (min-width: $screen-md-min){
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 2;
    }
  }
  @media (min-width: $screen-lg-min){
    .img-top + .header-absolute{
      top: 75px;
    }
  }
}

.header-absolute{
  // mobile
  .rd-navbar-fixed{
    .rd-navbar-panel{
      &:before{
        background: $gray-dark;
      }
    }
    // menu button
    .rd-navbar-toggle span{
      background-color: $white;
      &:before, &:after{
        background-color: $white;
      }
    }
    // search button
    .rd-navbar-search{
      right: 6px;
    }
    .rd-navbar-search-toggle{
      color: $white;
    }
  }
  
  // desktop
  
  .rd-navbar-fullwidth, .rd-navbar-static{
    &.rd-navbar{
      background: transparent;
      &.rd-navbar--is-stuck{
        background: $gray-dark;
      }
    }

    .rd-navbar-nav{
      > li{
        > a{
          color: $white;
          font-size: 15px;
          font-weight: 700;
        }
      }
    }
    .rd-navbar-search {
      .rd-navbar-search-toggle{
        color: $primary;
        &:hover{
          color: $white;
        }
      }
    }
  }
  
  // static
  .rd-navbar-static{
    &.rd-navbar--is-stuck{
      .rd-navbar-brand {
        a{
          max-width: 200px;
        }
      }
    }
    .rd-navbar-inner{
      max-width: 1440px;
    }
  }
}

// header-home-1
// --------------------------------------------

.header-home-1{
  .rd-navbar{
    background: linear-gradient(to bottom, #f3f3f3, #fefefe );
  }
  .rd-navbar-collapse{
    .contact-list{
       a{
         font-style: italic;
         color: $primary;
         transition: 250ms all ease-in-out;
         &:hover{
           text-decoration: none;
           color: $gray-dark;
         }
       }
     }
  }
  
  // mobile
  
  .rd-navbar-fixed{
    .rd-navbar-collapse{
      padding: 15px;
    }
  }
  
  // desktop  
  
  .rd-navbar-fullwidth{
    .rd-navbar-inner{
      max-width: 1440px;
      padding-top: 0;
      padding-bottom: 0;
    }
    .rd-navbar-top-panel{
      background: transparent;
      .contact-list{
        li{
          display: inline-block;
          vertical-align: middle;
          & + li{
            margin-left: 15px;
          }
        }
      }
    }
    .rd-navbar-panel{
      padding: 25px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .rd-navbar-aside-right{
      justify-content: space-between;
      margin-top: 0;
      border-bottom:  4px solid $gray-dark;
    }
    .rd-navbar-nav{
      > li{
        
        .rd-navbar-submenu-toggle{
          display: none;
        }
        & + li{
          margin-left: -4px;
        }
        > a{
          position: relative;
          padding: 25px 30px;
          color: $gray-dark;
          font-size: 15px;
          font-weight: 700;
          background: transparent;
          &:before{
            position: absolute;
            content: '';
            left: 50%;
            bottom: -4px;
            background: $white;
            width: 0;
            height: 4px;
            transform: translateX(-50%);
            transition: all .5s;
          }
          &:hover{
            background: $gray-dark;
            color: $white;
            &:before{
              width: 100%;
            }
          }
        }
        &.active, &.focus{
          > a{
            background: $gray-dark;
            color: $white;
            &:before{
              width: 100%;
            }
          }
        }
      }
    }
    
    .rd-navbar-search{
      .rd-navbar-search-toggle{
        display: none;
      }    
      .rd-search{
        visibility: visible;
        opacity: 1;
        position: relative;
        .form-label{
          font-style: italic;
        }
        .form-input{
          background: transparent;
          border-color: transparent;
        }
      }
    }
    
    // stuck
    
    &.rd-navbar--is-stuck{
      background: linear-gradient(to bottom, #f3f3f3, #fefefe );
      
      .rd-navbar-inner{
        padding-top: 0;
        padding-bottom: 0;
      }
    }
  }
}

