  background-position: 50%;
  background-size: 100%;
  background-repeat: no-repeat;
}

.swiper-lazy-preloader-white:after {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20viewBox%3D'0%200%20120%20120'%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20xmlns%3Axlink%3D'http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink'%3E%3Cdefs%3E%3Cline%20id%3D'l'%20x1%3D'60'%20x2%3D'60'%20y1%3D'7'%20y2%3D'27'%20stroke%3D'%23fff'%20stroke-width%3D'11'%20stroke-linecap%3D'round'%2F%3E%3C%2Fdefs%3E%3Cg%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(30%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(60%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(90%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(120%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(150%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.37'%20transform%3D'rotate(180%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.46'%20transform%3D'rotate(210%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.56'%20transform%3D'rotate(240%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.66'%20transform%3D'rotate(270%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.75'%20transform%3D'rotate(300%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.85'%20transform%3D'rotate(330%2060%2C60)'%2F%3E%3C%2Fg%3E%3C%2Fsvg%3E");
}

@keyframes swiper-preloader-spin {
  100% {
    transform: rotate(360deg);
  }
}

.swiper-slide > .vide__body,
.swiper-slide > .parallax_cnt {
  height: 100%;
}


// Swiper navigation
//--------------------------------------------------

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  display: none;

  font-size: 26px;
  line-height: 1;
  color: $white;

  text-align: center;
  cursor: pointer;
  transition: .2s ease-in;
  will-change: transform;
  z-index: 10;

  &:hover {
    color: $primary;
  }

  @media (min-width: $screen-sm-min) {
    display: block;
  }


  // Disabled button
  &.swiper-button-disabled{
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }
}

.swiper-button-prev {
  left: 30px;

  &:before{
    font-family: FontAwesome;
    content: "\f060";
  }
}

.swiper-button-next {
  right: 30px;

  &:before{
    font-family: FontAwesome;
    content: "\f061";
  }
}


.swiper-slider.swiper-container-rtl{
  .swiper-button-prev {
    &:before{
      content: "\f061";
    }
  }

  .swiper-button-next {
    &:before{
      content: "\f060";
    }
  }
}



// Swiper pagination
//--------------------------------------------------

.swiper-pagination {
  position: absolute;
  display: block;
  text-align: center;
  transition: .3s;
  transform: translate3d(0, 0, 0);
  z-index: 10;

  &.swiper-pagination-hidden {
    opacity: 0;
  }

  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    display: inline-block;
    border-radius: 100%;
    background: transparent;
    transition: .2s;
    border: 1px solid $white;

    &.swiper-pagination-bullet-active,
    &:hover{
      background: rgba(255,255,255,1);
    }
  }

  &.swiper-pagination-clickable .swiper-pagination-bullet {
    cursor: pointer;
  }
}

// Dark pagination
.swiper-pagination-black{
  .swiper-pagination-bullet {
    background: rgba(0,0,0,.6);

    &.swiper-pagination-bullet-active {
      background: rgba(0,0,0,1);
    }
  }
}


// Swiper pagination orientation
//--------------------------------------------------

// Vertical pagination
.swiper-container-vertical{
  > .swiper-pagination {
    right: 10px;
    top: 50%;
    transform: translate3d(0px, -50%, 0);

    .swiper-pagination-bullet {
      margin: 5px 0;
      display: block;
    }
  }
}


// Horizontal pagination
.swiper-container-horizontal{
  > .swiper-pagination {
    bottom: 20px;
    left: 0;
    width: 100%;

    .swiper-pagination-bullet {
      margin: 0 5px;
    }
  }
}


// Swiper slide styles
//--------------------------------------------------

.swiper-slide {
  position: relative;

  @include display-flex();
  @include align-items(center);
  @include justify-content(center);
  @include flex-shrink(0);
  width: 100%;
  height: 100%;

  white-space: nowrap;

  @extend %context-dark;

  &-caption {
    width: 100%;
    white-space: normal;

    p {
      display: none;

      @media (min-width: $screen-sm-min) {
        display: block;
      }
    }
  }
}

.swiper-slider-type-1{
  &.swiper-container{
    height: auto;
  }
  
  .swiper-button-prev, .swiper-button-next{
    display: none;
  }
  
  .swiper-pagination {
    text-align: left;
    left: 20px;
    @media (min-width: $screen-md-min) {
      left: 30px;
    }
  }
  .post-type-1{
    .caption{
      bottom: 60px;
      
    }
  }
}  

.swiper-slider-type-2{
  &.swiper-container{
    height: calc(100vh - 200px);
    min-height: 280px;
    @media (min-width: $screen-lg-min){
      height: calc(100vh - 80px);
    }
    @media (min-width: $screen-lg-min + 240px){
      height: calc(100vh - 200px);
    }
  }
  .swiper-slide{
    text-align: left;
    justify-content: flex-start;
    align-items: center;
    
    > *{
      position: relative;
      z-index: 2;
    }
    &:before{
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      box-shadow: inset 0px 150px 700px rgba(0, 0, 0, 0.5);
      z-index: 1;
    }
    &:after{
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba($gray-darker, .4);
      box-shadow: inset 0px -150px 700px rgba(0, 0, 0, 0.5);
      z-index: 1;
    }
    
    @media (min-width: $screen-md-min) {
     align-items: flex-end;   
    }
    
    .tag-list + *{
      margin-top: 20px;
    }
    
    .title{
      line-height: 1.2;
      a{
        color: $white;
        &:hover{
          text-decoration: underline;
        }
      }
      
      & + *{
        margin-top: 20px;
        @media (min-width: $screen-sm-min) {
          margin-top: 30px;   
        }
      }
    }
    .caption{
      padding-bottom: 30px;
      @media (min-width: $screen-md-min){
        padding-bottom: 50px;
      }
      
      .meta-list{
        li{
          color: $white;

        }
      }
    }
  }
  > .swiper-pagination{
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    bottom: 15px;
    padding-left: 15px;
    padding-right: 15px;
    text-align: left;
    left: 50%;
    transform: translateX(-50%);
  }
}

.swiper-slider-type-3 {
  &.swiper-container {
    height: calc(100vh - 100px);
    min-height: 280px;
  }
  .swiper-slide{
    &:after{
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba($gray-darker, .4);
      box-shadow: inset 0px -150px 700px rgba(0, 0, 0, 0.5);
    }
    &:before{
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      box-shadow: inset 0px 150px 700px rgba(0, 0, 0, 0.5);
      z-index: 1;
    }
    
  }
  .swiper-slide-caption{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    h1{
      color: $white;
    }
    p {
      color: $white;
      display: none;

      @media (min-width: $screen-sm-min) {
        display: block;
      }
    }
    .button-block{
      margin-left: -5px;
      margin-right: -5px;
      .button{
        @media (max-width: $screen-xs-min){
          margin-top: 10px;
        }
        
        margin-left: 5px;
        margin-right: 5px;
      }
    }
  }
}

.swiper-slider{
  .title{
    a{
      transition: 250ms all ease-in-out;
      &:hover{
        text-decoration: underline;
      }
      
    }
  }
}
.swiper-slider-type-1{
  .post-type-1.post-type-1-mode{
    @media (min-width: $screen-sm-min) {
     img {
       position: relative;
       width: 100%;
     }   
    }
    @media (min-width: $screen-lg-min){
      img{
        width: inherit;
      }
    }
  }
}
