/*
*
* Quotes
* --------------------------------------------------
*/

.quote-default{
  max-width: 400px;
  q{
    position: relative;
    display: block;
    padding-top: 60px;
    padding-bottom: 25px;
    &:before{
      position: absolute;
      top: 0;
      left: 0;
      content: '\f10d';
      font-family: 'FontAwesome';
      color: $gray-darker;
      font-size: 34px; 
    }
  }
  .name{
    border-top: 1px solid $gray-light-7;
    padding-top: 20px;
    letter-spacing: -.02em;
    cite{
      display: block;
      font-style: normal;
    }
    span{
      font-size: 15px;
      line-height: 20px;
      color: $gray-light-5;
    }
  }
}