/*
*
* Buttons
* --------------------------------------------------
*/


// Button General Styles

.button {
  display: inline-block;
  position: relative;
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(13px, 25px, 15px, 20px, 5px);
  white-space: nowrap;
  overflow: hidden;
  font-weight: 700;
  text-overflow: ellipsis;
  border-style: solid;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  user-select: none;
  min-width: 165px;
  transition: 250ms all ease-in-out;

  &:focus,
  &:active,
  &:active:focus {
    outline: none;
  }

  &.disabled{
    color: black;
    background-color: gray;
  }

  .caret{
    margin-left: 8px;
  }
}

.button-block{
  display: block;
  width: 100%;
}


// Appearance style
// --------------------------------------------------

.button-default {
  @include btn-variant($gray-darker, transparent, $gray, $white, $gray-darker, $gray-darker);
}

.button-primary {
  @include btn-variant($white, $primary, $primary, $white, $secondary, $secondary);
}

.button-white {
  @include btn-variant($gray-light-8, $white, $white, $white, $secondary, $secondary);
}

.button-steel-blue {
  @include btn-variant($white, #547ABB, #547ABB, $white, darken(#547ABB, 10%), darken(#547ABB, 10%));
  width: 165px;
  text-transform: uppercase;
  font-weight: 400;
  border-radius: 4px;
}

.button-outline{
  @include btn-variant($gray-light-8, $white, #cccccc, $white, #cccccc, #cccccc);
}


.button-cerulean {
  @include btn-variant($white, #44c0f3, #44c0f3, $white, darken(#44c0f3, 5%), darken(#44c0f3, 5%));
  width: 165px;
  text-transform: uppercase;
  font-weight: 400;
  border-radius: 4px;
}

.button-mandy {
  @include btn-variant($white, #e75854, #e75854, $white, darken(#e75854, 10%), darken(#e75854, 10%));
  width: 165px;
  text-transform: uppercase;
  font-weight: 400;
  border-radius: 4px;
}

// Btn with shadow
.button-shadow{
  box-shadow: 0 3px 11px 0 rgba(0, 0, 0, 0.15);

  &:hover{
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.15);
  }
}


// Button Sizes
// --------------------------------------------------

.button-xs {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(6px, 28px, 15px, 24px, 5px);
}



.button-lg {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(16px, 38px, 15px, 24px, 5px);
}

.button-xl {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(22px, 58px, 15px, 24px, 5px);
}


// Button Shapes
// --------------------------------------------------

.button-circle {
  border-radius: 30px;
}

.button-ellipse {
  border-radius: 10px;
}

.button-square{
  border-radius: 0;
}

// Button Icon styles
// --------------------------------------------------

.button {
  &.button-icon {
    padding-left: 17px;
    padding-right: 17px;

    .icon {
      font-size: 24px;
      line-height: inherit;
      vertical-align: middle;
      transition: 0s;
      &.icon-sm{
        font-size: 22px;
      }
    }

    &-left {
      .icon {
        float: left;
        padding-right: 7px;
      }
    }

    &-right {
      .icon {
        float: right;
        padding-left: 7px;
      }
    }
  }
}



