}


// Thumb
//

.thumb{
  position: relative;
  display: inline-block;

  // Bg overlay
  &:before{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    background-color: rgba($primary, .4);
    z-index: 1;
    content: '';
    will-change: opacity;
  }

  // Icon
  &:after{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    color: $white;
    font-family: FontAwesome;
    font-size: 34px;

    content: "\f065";
    z-index: 1;
    will-change: transform, opacity;
  }

  &:before,
  &:after{
    transition: .3s ease;
    opacity: 0;
  }

  &:hover{
    &:before,
    &:after{
      opacity: 1;
    }
  }
}


.thumbnail-instafeed{
  position: relative;
  
  .caption{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba($primary, .5);
    opacity: 0;
    transition: all .3s;
    ul.list-inline{
      position: absolute;
      top: 50%;
      left: 50%;
      color: $white;
      text-align: center;
      transform: translate(-50%, -50%);
      >li{
        padding-left: 1px;
        padding-right: 1px;
      }
    }
  }
  &:hover{
    .caption{
      opacity: 1;
    }
  }
}