/*
*
* Pagination custom
* --------------------------------------------------
*/

//== Pagination variables
//

$pagination-padding-vertical: 12px;
$pagination-padding-horizontal: 17px;

$pagination-font-size: 15px;
$pagination-lh-size: 20px;
$pagination-border-w: 2px;

$pagination-color:                     $gray-dark;
$pagination-bg:                        transparent;
$pagination-border:                    transparent;

$pagination-hover-color:               #fff;
$pagination-hover-bg:                  $primary;
$pagination-hover-border:              $primary;

$pagination-active-color:              #fff;
$pagination-active-bg:                 $primary;
$pagination-active-border:             $primary;

$pagination-disabled-color:            $gray-light;
$pagination-disabled-bg:               #fff;
$pagination-disabled-border:           #ddd;


.pagination-custom {
  font-size: 0;

  > li {
    margin: 0;
    font-style: italic;
    &,
    > a,
    > span{
      display: inline-block;
    }
    &.dot{
      font-size: 15px;
      margin: 0 5px;
    }
    > a,
    > span {
      @media (max-width: $screen-xs-min - 1) {
        font-size: 13px;
        padding: 6px 10px;
      }
      padding: $pagination-padding-vertical $pagination-padding-horizontal;
      font-size: $pagination-font-size;
      line-height: $pagination-lh-size;
    }
  }

  > li > a,
  > li > span {
    @media (max-width: $screen-xs-min - 1) {
      min-width: 36px;
    }
    min-width: $pagination-padding-vertical*2 + $pagination-border-w*2 + $pagination-lh-size;
    font-weight: 400;
    transition: .3s;
    text-align: center;
    background-color: transparent;
    color: $pagination-color;
    border: $pagination-border-w solid $pagination-border;
    //box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.10);
  }

  > li:first-child,
  > li:last-child {
    

    a, span {
      span {
        position: relative;
        font-size: 11px;
        line-height: 1;
      }
    }
  }
  > li:last-child{
    font-style: normal;
    font-weight: 700;
    a{
      color: $pagination-hover-color;
      background-color: $pagination-hover-bg;
      border-color: $pagination-hover-border;
      &:hover{
        background-color: $secondary;
        border-color: $secondary;
      }
    }
  }
  > li > a,
  > li > span {
    &:hover,
    &:focus,
    &:active,
    &:active:focus {
      color: $pagination-hover-color;
      background-color: $pagination-hover-bg;
      border-color: $pagination-hover-border;
      box-shadow: none;
    }
  }

  > .active > a,
  > .active > span {
    &,
    &:hover,
    &:focus,
    &:active,
    &:active:focus {
      color: $pagination-active-color;
      background-color: $pagination-active-bg;
      border-color: $pagination-active-border;
      box-shadow: none;
    }
  }
  > .active{
    pointer-events: none;
  }
}

