/*
*
* Footers
* --------------------------------------------------
*/

.page-footer-default{
  padding: 25px 0 50px;
  border-top: 4px solid $gray-dark;
  
  .privacy{
    font-style: italic;
    color: $gray-light-2;
  }
  * + .privacy{
    margin-top: 20px;
  }
  &.page-footer-type-1{
    padding-top: 35px;
    .footer-title + * {
      margin-top: 22px;
    }
    .list{
      li{
        a{
          font-style: italic;
          color: $gray-light;
          transition: all .3s;
          &:hover{
            color: $primary;  
          }
        }
        & + li{
          margin-top: 15px;
        }
      }
    }
    .footer-bottom-section{
      border-top: 1px solid $gray-dark;
      padding-top: 25px;
    }
    .footer-post-list{
      padding-right: 20px;
      li + li{
        margin-top: 25px;
      }
      a{
        font-weight: 600;
        color: $gray-darker;
        &:hover{
          color: $primary;
        }
      }
    }
  }
}
