// Import variables
@import "variables-custom";
// Import mixins
@import "mixins";

/*
*
* Main Styles
* ==================================================
*/

// Remove leading spacing of element
html {
  *:first-child {
    margin-top: 0;
  }

  *:last-child {
    margin-bottom: 0;
  }
}

// Body

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  color: $text-color;
  background-color: $body-bg;
}

// Links

a {
  color: $link-color;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    outline: none;
  }

  &[href*='callto'],
  &[href*='mailto'] {
    white-space: nowrap;
  }
}

.link-hover {
  color: $link-hover-color;
}

.link-press {
  color: $link-press-color;
}

.p a:hover,
.list a:hover {
  text-decoration: underline;
}

// Images

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

/*
*
* Typography
* ==================================================
*/

#{headings()} {
  margin-top: 0;
  margin-bottom: 0;

  font-weight: $headings-font-weight;
  color: $headings-color;

  a {
    transition: .3s all ease;
  }

  a:hover {
    color: darken($primary, 15%);
  }
}

h1,
.heading-1 {
  font-size: 36px;
  line-height: 1.5;
  letter-spacing: -.04em;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h1;
    font-size: $font-size-h1;
  }
}

h2,
.heading-2 {
  font-size: 32px;
  line-height: 1.5;
  letter-spacing: -.04em;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h2;
    font-size: $font-size-h2;
  }
}

h3,
.heading-3 {
  font-size: 28px;
  line-height: 1.5;
  letter-spacing: -.04em;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h3;
    font-size: $font-size-h3;
  }
}

h4,
.heading-4 {
  font-size: 24px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h4;
    font-size: $font-size-h4;
  }
}

h5,
.heading-5 {
  font-size: 18px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h5;
    font-size: $font-size-h5;
  }
}

h6,
.heading-6 {
  font-size: 17px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h6;
    font-size: $font-size-h6;
  }
}

// Big text
.big {
  font-size: $font-size-large;
}

// Small text
small,
.small {
  display: block;
  font-size: $font-size-small;
}

mark {
  background: $primary;
  color: $white;
}

// Divider
// -------------------------

hr {
  margin-top: 0;
  margin-bottom: 0;
  border-top: 1px solid $gray-lighter;
}

.divider {
  margin-top: 0;
  margin-bottom: 0;
  border: none;

  height: 2px;
  width: 60px;
  background-color: $gray-base;
  &-gray {
    height: 1px;
    width: 100%;
    background-color: $gray-lighter-2;
  }

}

.block-line {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  &:before, &:after {
    content: '';
    border-top: 1px solid $gray-lighter-2;
    width: 175px;
  }
  > * {
    margin: 0 30px;
    @media (min-width: $screen-sm-min) {
      margin: 0 65px;
    }
  }
}

* + .block-line {
  margin-top: 25px;
}

/*
*
* Backgrounds
* ==================================================
*/
// bg-behaviour() off padding top if two section with same bg that are beside

.bg-white {
  @include bg-behaviour($white);
}

.bg-dark {
  @include bg-behaviour($gray-darker);
}

.bg-gray {
  @include bg-behaviour(#efefef);
}

.bg-primary {
  @include bg-behaviour($primary);

  // Change color of primary button on primary bg
  .btn-primary {
    @include btn-variant($primary, $white, $white, $primary, darken($white, 5%), darken($white, 5%));
  }
}

.bg-image {
  background-size: cover;
  background-position: center center;
}

html.desktop {
  .bg-fixed {
    @media (min-width: $screen-md-min) {
      background-attachment: fixed;
    }
  }
}

.img-link {
  img {
    transition: all .3s;
  }
  & + h4 {
    margin-top: 20px;
    a {
      color: $gray-darker;
      &:hover {
        color: $primary;
      }
    }

  }
  &:hover {
    img {
      box-shadow: 0 0 42px rgba($gray-base, .14);
    }
  }
}

/*
*
* Colors
* ==================================================
*/

.page {
  @include text-variant('.text-gray-base', $gray-base, $primary);
  @include text-variant('.text-gray-darker', $gray-darker, $primary);
  @include text-variant('.text-gray-dark', $gray-dark, $primary);
  @include text-variant('.text-gray', $gray, $primary);
  @include text-variant('.text-red', #fb6666, $primary);
  @include text-variant('.text-gray-light', $gray-light, $primary);
  @include text-variant('.text-gray-light-6', $gray-light-6, $primary);
  @include text-variant('.text-gray-lighter', $gray-lighter, $primary);
  @include text-variant('.text-primary', $primary, darken($primary, 10%));
  @include text-variant('.text-white', $white, darken($white, 10%));
}

.bg-animate {
  background-repeat: repeat;
  animation: bgAnimate ease-in-out infinite 15s alternate;
}

@keyframes bgAnimate {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 0 -1000px;
  }
}

/*
*
* Main layout
* ==================================================
*/

// Page
//

.page {
  position: relative;
  z-index: 1;
  overflow: hidden;
  min-height: 100vh !important;
}

// Page header
//

.page-header {
  //Kill bootstrap style
  padding: 0;
  margin: 0;
  border-bottom: none;
}

// Page footer
//

.page-footer {
  padding-top: 40px;
  padding-bottom: 30px;
  background-color: #f3f3f3;
}

// Components & modules & plugins & helpers & fonts
//

.section-typography-body {
  h1 + p,
  h2 + p {
    margin-top: 40px;
  }
  p + h2 {
    margin-top: 100px;
  }
  p + h3 {
    margin-top: 90px;
  }
  h3 + p {
    margin-top: 35px;
  }
  p + h4,
  p + h5,
  p + h6 {
    margin-top: 75px;
  }
  h4 + p {
    margin-top: 15px;
  }
  h5 + p,
  h6 + p {
    margin-top: 10px;
  }
}

.section-title {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  text-align: left;
  border-bottom: 1px solid $gray-dark;
  h3{
    padding-bottom: 10px;
  }
 
  & + .range, & + * {
    margin-top: 30px;
  }
  &-type-1 {
    border-bottom: none;
    padding-bottom: 0;
  }
}

// graphiq
// -------------------------------------------------

.graphiq-block {
  .ww-root.ww-has-shadow > .ww-root-inner {
    box-shadow: none !important;
  }
  .ww-root > .ww-root-inner {
    border: none !important;
  }
}

// contact-box
// ------------------------------------------------------

.contact-box {
  text-align: left;
  h2 + * {
    margin-top: 35px;
  }
  .contact-list {
    font-size: 18px;
    font-weight: 600;
    color: $gray-dark;
    li + li{
      margin-top: 10px;
      @media (min-width: $screen-xs-min){
        margin-top: 0;
      }
    }
    a {
      text-decoration: underline;
      color: $primary;
      &:hover {
        text-decoration: none;
      }
    }
    & + * {
      margin-top: 35px;
    }
  }

  h3 + * {
    margin-top: 30px;
  }
  .big {
    font-weight: 600;
    color: $gray-dark;
  }
}

// author
// --------------------------------------------------

.author-block {
  text-align: left;
  padding: 22px;
  background: $gray-lighter-1;
}

// comments
// --------------------------------------------------

.comment-block {
  h3 + * {
    margin-top: 40px;
  }
}

.comment-item + .comment-item, .comment-item + .comment-item-reply, .comment-item-reply + .comment-item {
  margin-top: 50px;
  @media (min-width: $screen-xs-min){
    margin-top: 20px;
  }
}

.comment-item + .comment-item, .comment-item-reply + .comment-item {
  border-top: 1px solid #e7e7e7;
  padding-top: 20px;
}

.comment-item, .comment-item-reply {
  .top-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.comment-item-reply {
  @media (min-width: $screen-sm-min) {
    margin-left: 60px;
  }
}

// modal
// --------------------------------------------------

.modal {
  .modal-dialog {
    max-width: 280px;
    margin: 40px auto 0;
    @media (min-width: $screen-xs-min){
      max-width: 370px;
    }
    @media (min-width: $screen-sm-min) {
      margin-top: 100px;
      margin-right: 50px;
    }
    
    @media (min-width: $screen-lg-min + 240px){
      margin-right: calc((100vw - 1440px)/2);
    }
    
    .modal-content {
      padding: 15px;
      border-radius: 0;
      @media (min-width: $screen-xs-min){
        padding: 30px;
      }
    }
    .modal-title + * {
      margin-top: 15px;
    }
    .button.button-block{
      width: 100%;
    }
    p{
      margin: 10px 0;
    }
    .close{
      position: absolute;
      top: -15px;
      right: -15px;
      width: 30px;
      height: 30px;
      font-weight: 300;
      color: $white;
      border-radius: 50%;
      background: $primary;
      opacity: 1;
      outline: none;
    }
    .form-input {
      border-radius: 0;
      padding: 10px 20px 10px 50px;
      font-style: italic;
      color: $gray-light-3;
    }
    .form-label {
      padding-right: 4px;
      border-right: 1px solid $gray-lighter;
    }
    .form-wrap + .form-wrap {
      margin-top: 10px;
    }
    * + .rd-mailform{
      margin-top: 10px;
    }
    .form-footer {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .button {
        border-radius: 0;
        min-width: 120px;
        @media (min-width: $screen-xs-min){
          min-width: 150px; 
        }
        
      }
    }
    input[type="checkbox"] {
      margin-top: 5px;
      outline: none;
      display: none;
      &:checked + label{
        &:after{
          opacity: 1;
        }
      }
    }
    .checkbox-inline {
      font-style: italic;
      color: $gray-light-3;
      position: relative;
      &:before{
        position: absolute;
        content: '';
        left: 0;
        top: 3px;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        border: 1px solid $gray-light-3;
      }
      &:after{
        position: absolute;
        content: '';
        left: 3px;
        top: 6px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: $gray-dark-2;
        opacity: 0;
      }
    }
  }
}
.unit-landing{
  max-width: 360px;
  margin-left: auto;
  margin-right: auto;
  
}

.graphiq-block{
  iframe{
    border: none;
    overflow-scrolling: none;
  }
}

@import "includes";
@import "components/_layout-panel";