/*
*
* Counter
* --------------------------------------------------
*/


.counter {

  &__subtitle{}

  &-wrap{
    .heading-1{
      color: $white;
    }
    p{
      color: #959799;
      font-size: 18px;
      text-transform: uppercase;
      font-weight: 600;
    }
    .counter-text-type-1{
      color: #91fe91;
    }
    .divider{
      margin-left: auto;
      margin-right: auto;
      margin-top: 10px;
      background: $primary;
      height: 5px;
      &-white{
        background: $white;
      }
    }
  }
}
