/*
*
* Posts
* --------------------------------------------------
*/


.post-blog{

  &__title{

  }
}

.bottom-block{
  display: flex;
  justify-content: space-between;
  align-items: center;
  &.hidden-block{
    justify-content: flex-end;
    .meta-list, .tag-list-type-1{
      opacity: 0;
      position: absolute;
    }
    .share-block{
      .soc-icon { 
        display: block;
      }
    }
  }
}
.bottom-block-type-1{
  @media (min-width: $screen-xs-min) and (max-width: $screen-xs-min + 150px) {
    height: 52px;
    .share-block .soc-icon .icon{
      width: 30px;
    }
  }
}

.share-block{
  height: 38px;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  > .fa-share-alt{
    display: inline-block;
    color: $primary;
    font-size: 20px;
    line-height: 20px;
    transform: rotateY(180deg);
    cursor: pointer;
    transition: .3s all;
    &:hover{
      color: $secondary;
    }
  }
  * + .fa-share-alt{
    margin-left: 5px;
  }

  .soc-icon{
    width: 100%;
    display: none;
    transition: .3s all;
    .icon{
      width: 38px;
      height: 38px;
      font-size: 16px;
      margin-left: 0;
      margin-right: 0;
      background: transparent;
      color: $primary;
      &:hover{
        color: $secondary;
      }
    }
  }
}

.post-type-1{
  position: relative;
  min-height: 330px;
  overflow: hidden;
  
  
  &:after{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to top, rgba(10,10,10,1) 0%,rgba(10,10,10,0) 70%);
    z-index: 1;

  }
  
  img{
    position: absolute;
    left: 50%;
    max-width: inherit;
   
    transform: translateX(-50%);
    z-index: 0;

    
    
    @media (min-width: $screen-sm-min) {
      width: inherit;
    }
    
    @media (min-width: $screen-md-min){
      position: relative;
      width: 100%;
      height: 100%;
    }
    @media (min-width: $screen-lg-min){
      width: inherit;
    }
  }
  
  .tag-list{
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 2;
    @media (min-width: $screen-md-min){
      top: 30px;
      left: 30px;
    }
  }
  
  .caption{
    position: absolute;
    left: 20px;
    right: 20px;
    bottom: 20px;
    text-align: left;
    z-index: 2;
    @media (min-width: $screen-md-min){
      left: 30px;
      right: 30px;
      bottom: 18px;
    }
    
    .title{
      line-height: 1.1;
      a{
        color: $white;
        &:hover{
          text-decoration: underline;
        }
      }
      @media (min-width: $screen-lg-min){
        padding-right: 100px;
      }
    }
    .title + *{
      margin-top: 10px;
    }
    .meta-list{
      li{
        color: $white;
      }
    }
  }
  .share-block{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    z-index: 2;
    
    
    > .fa-share-alt{
      display: inline-block;
      color: $white;
      font-size: 20px;
      line-height: 20px;
      transform: rotateY(180deg);
      cursor: pointer;
      transition: .3s all;
      &:hover{
          color: $primary;
      }
    }
    * + .fa-share-alt{
      margin-left: 5px;
      
    }
    
    
    
    .soc-icon{
      .icon{
        color: $white;
        &:hover{
          color: $primary;
        }
      }
    }
  }
  &.post-type-1-mode{

   
      img{
        @media (min-width: $screen-md-min){
          position: absolute;
          width: inherit;
        }
      }
  }
  &.post-type-1-mode-1{
    img{
      @media (min-width: $screen-sm-min - 100px) and (max-width: $screen-xs-max) {
        width: 100%;
      }
      @media (min-width: $screen-md-min){
        width: inherit;
      }
    }
  }
}

.post-type-2{
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 10px;
  text-align: left;
  border-bottom: 1px solid $gray-light-3;
  .img-block{
    position: relative;
    overflow: hidden;
    
    img{
      width: 100%;
      transition: .3s all;
    }
    .tag-list{
      position: absolute;
      left: 30px;
      top: 30px;
    }
    &:hover{
      img{
        transform: scale(1.01);
      }
    }
  }
  .img-block + .caption{
    margin-top: 30px;
  }
  .caption{
    h5{
      a{
        color: $gray-dark;
        &:hover{
          color: $primary;
        }
      }
    }
    h5 + *{
      margin-top: 18px;
    }
  }
  * + .button-block{
    margin-top: 18px;
  }
}

.post-type-3{
  text-align: left;
  padding-bottom: 20px;
  border-bottom: 1px solid $gray-light-3;
  .img-block{
    position: relative;
    overflow: hidden;

    img{
      transition: .3s all;
    }
    .tag-list{
      position: absolute;
      left: 10px;
      top: 10px;
    }
    &:hover{
      img{
        transform: scale(1.01);
      }
    }
  }
  .bottom-block{
    align-items: flex-start;
    .share-block{
      height: 26px;
    }
  }
  .title{
    font-weight: 600;
    a{
      color: $gray-dark;
      &:hover{
        color: $primary;
      }
    }
    & + .bottom-block{
      margin-top: 25px;
    }
    & + *{
      margin-top: 20px;
    }
    @media (min-width: $screen-lg-min) and (max-width: 1340px) {
      font-size: 16px;
      line-height: 22px;
      & + .bottom-block{
        margin-top: 18px;
      }
    }
  }
  * + .bottom-block{
    margin-top: 20px;
  }
 
  
  
  &.post-type-3-modern{
    .bottom-block{
      position: relative;
      display: block;
      padding-right: 30px;
      min-height: 63px;
      @media (min-width: $screen-sm-min){
        display: flex;
        justify-content: flex-start;
        min-height: 32px;
      }
    }
    .share-block{
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
    }
    .tag-list-type-1{
      li{
        a{
          color: $gray-light-3;
          font-size: 11px;
          padding: 5px;
          &:hover{
            color: $white;
          }
        }
      }

    }
    * + .tag-list-type-1{
      margin-top: 5px;
      @media (min-width: $screen-sm-min){
        margin-top: 0;
        margin-left: 20px;
      }
    }
  }
  .unit__body{
    flex: 1 auto;
  }
}


.post-type-3 + .post-type-3{
  margin-top: 20px;
}

.post-video{
  text-align: left;
  * + .caption{
    margin-top: 25px;
  }
  .title{
    a{
      font-weight: 600;
      color: $gray-dark;
      &:hover{
        color: $primary;
      }
    }
    & + *{
      margin-top: 20px;
    }
  }
 
}

.post-list{
  text-align: left;
  a{
    font-size: 18px;
    line-height: 26px;
    font-weight: 600;
    color: $gray-dark;
    &:hover{
      color: $primary;
    }
  }
  li{
    padding-bottom: 10px;
    border-bottom: 1px solid $gray-lighter-3;
    &:last-child{
      border-bottom: none;
    }
    & + li{
      margin-top: 10px;
    }
  }
}

.post-big{
  position: relative;
  text-align: left;
  padding: 20px;
  z-index: 1;
  
  @media (min-width: $screen-sm-min) {
    padding:30px 20px 60px;   
  }
  > *{
    position: relative;
    z-index: 2;
  }

  &:after{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba($gray-base, .2);;
    box-shadow: inset 0 -260px 60px rgba($gray-base, .6);
    z-index: 1;
  }

  .logo{  
    width: 100px;
    @media (min-width: $screen-xs-min){
      width: 200px;
    }
    @media (min-width: $screen-sm-min) {
      width: auto;   
    }
  }
  
  .logo + *{
    margin-top: 55px;
    @media (min-width: $screen-sm-min) {
      margin-top: 70px;
    }
    @media (min-width: $screen-lg-min){
      margin-top: 85px;
    }
  }
  
  .numb-block{
    display: inline-block;
    padding: 10px 30px;
    background: $white;
    margin-left: -20px;
  }
  .numb-block + h1{
    margin-top: 25px;
  }
  h1{
    color: $white;
    line-height: 1.2;
  }
  * + .caption{
    margin-top: 60px;
    @media (min-width: $screen-sm-min) {
      margin-top: 70px;
    }
    @media (min-width: $screen-lg-min){
      margin-top: 85px;
    }
  }
  .caption{
    .title{
      line-height: 1.2;
      a{
        color: $white;
        &:hover{
          color: $primary;
        }
      }
      & + *{
        margin-top: 15px;
      }
    }
  }
}


.blog-post{
  text-align: left;
  
  h2 + img{
    margin-top: 20px;
  }
  img + *{
    margin-top: 40px;
  }
}
.post-content{
  text-align: left;
  padding-bottom: 45px;
  * + img{
    margin-top: 20px;
  }
  img + *{
    margin-top: 30px;
  }
  p + p{
    margin-top: 25px;
  }
  .tag-list + *{
    margin-top: 10px;
  }
  h2 + *, h3 + *{
    margin-top: 10px;
  }
  .tag-block{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .left-block {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      > * + *{
        margin-left: 8px;
      }
      p{
        margin-top: 0;
        margin-bottom: 0;
      }
    }
    .tag-list{
      a{
        font-size: 11px;
        background: $gray-lighter-2;
        color: $gray-light-3;
        &:hover{
          background: $gray-dark;
        }
      }
    }
  }
  * + .tag-block{
    margin-top: 25px;
  }
}

.post-nav{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  margin-top: 35px;
  padding-top: 30px;
  border-top:  1px solid $gray-dark;
  
  @media (min-width: $screen-xs-min){
    flex-direction: row;
  }
  
  @media (min-width: $screen-sm-min) {
    padding-top: 40px;   
  }
  .prev-post, .next-post{
    text-align: left;
    a{
      color: $gray-dark;
      &:hover{
        color: $primary;
      }
    }
  }
  .next-post{
    margin-top: 40px;
    text-align: left;
    @media (min-width: $screen-xs-min){
      margin-top: 0;
      text-align: right;
    }
  }
  .next-post,.prev-post{
    @media (min-width: $screen-xs-min){
      flex-basis: 50%;
    }
    > a{
      font-style: italic;
    }
    > a + h5{
      margin-top: 15px;
      @media (min-width: $screen-xs-min){
        margin-top: 25px;
      }
    }
    h5{
      font-weight: 600;
    }
    
  }
}