/*
*
* Sections
* --------------------------------------------------
*/


// Section Spacing
// -------------------------

.section-xs{
  padding-top: 30px;
  padding-bottom: 30px;
  &-type-1{
    padding-top: 30px;
    padding-bottom: 90px;
  }
}

.section-sm {
  padding-top: 60px;
  padding-bottom: 60px;
}

.section-md {
  padding-top: 90px;
  padding-bottom: 90px;
}

.section-lg {
  padding-top: 120px;
  padding-bottom: 120px;
}

.section-xl{
  padding-top: 120px;
  padding-bottom: 120px;
  @media (min-width: $screen-md-min){
    padding-top: 200px;
    padding-bottom: 200px;
  }
}

.section-bottom-60{
  padding-bottom: 60px;
}

.section-bottom-120{
  padding-bottom: 120px;
}

// Section Styles
// -------------------------

.section-relative {
  position: relative;
  z-index: 1;
}

// Section Grid Demonstration
.section-grid-demonstration {
  @media (max-width: $screen-xs-min) {
    [class^="col"] {
      padding: 5px;
    }
  }
  .grid-demonstration-item {
    background: transparent;
    border: 1px solid $gray-lighter-2;
    margin-left: -15px;
    margin-right: -15px;
    @media (max-width: $screen-xs-min) {
        margin-left: -5px;
        margin-right: -5px;
    }

    @media (min-width: $screen-sm-min) {
      padding: 35px 30px;
      text-align: left;
    }

    @media (max-width: $screen-md-max) {
      h3 {
        font-size: 16px;
        line-height: 16px;
      }
      p {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
      }
    }
  }

  .divider + .row {
    margin-top: 35px;
  }
}

.section-left-sidebar{
  padding-bottom: 60px;
  @media (min-width: $screen-md-min){
    padding-top: 87px;
    &.section-left-sidebar-type-1{
       padding-top: 0;
     }
  }
  
  
  
  .soc-icon{
    .icon{
      width: 40px;
      height: 40px;
      @media (min-width: $screen-xs-min){
        width: 48px;
        height: 48px;
      }
      @media (min-width: $screen-md-min) and (max-width: $screen-lg-min - 1px ){
        width: 40px;
        height: 40px;
      }
    }
  }
  
  .rd-mailform-inline{
    @media (min-width: $screen-sm-min) and (max-width: $screen-lg-min - 1px ) {
      display: block;

      .button {
        margin-left: 0;
        margin-top: 10px;
      }
    } 
  }
  
}
.img-top{
  border-bottom: 1px solid #e8e8e8;
  iframe{
    width: 100%;
    overflow: hidden;
    margin-bottom: -10px;
  }
}