/*
*
* Lists
* --------------------------------------------------
*/


ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}


.list {
  > li + li {
    margin-top: 10px;
  }
}


// List inline
//

.list-inline {
  margin-left: -5px;
  margin-right: -5px;

  > li {
    padding-left: 5px;
    padding-right: 5px;
  }
}


// Description Lists
//

dl {
  margin: 0;
}


// List terms
//

.list-terms {
  margin-top: 35px;
  dt + dd { margin-top: 5px; }
  dd + dt { margin-top: 25px; }
}


// List index
//

.list-index {
  counter-reset: li;

  li {
    .list-index-counter {
      &:before {
        content: counter(li, decimal)". ";
        counter-increment: li;
        color: $primary;
        font-weight: 700;
      }
    }
  }
}


// List marked
//

.list-marked {
  li {
    position: relative;
    padding-left: 16px;

    &:before {
      content: '';
      display: inline-block;
      background: $gray-dark;
      width: 5px;
      height: 5px;
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 50%;
      transform: translateY(-50%);
    }
  }
}

.list-arrow{
  li {
    position: relative; 
    padding-left: 20px;

    &:before {
      content: '\f14f'; 
      display: inline-block;
      color: $primary;
      font-family: 'Material Design Icons';
      font-size: 14px;
      line-height: 14px;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.icon-list-title{
  text-transform: uppercase;
  font-weight: 600;
  color: $gray-light-8;
}



.tag-list{
  li{
    display: inline-block;
    vertical-align: middle;
    a{
      display: inline-block;
      background: $primary;
      color: $white;
      line-height: 20px;
      font-style: italic;
      padding: 5px  7px;
      transition: .3s all;

      &:hover{
        background: $secondary;
      }
    }
  }
}

.tag-list-type-1{
  margin-left: -1px;
  margin-right: -1px;
  font-size: 0;
  li{
    margin: 0 1px 2px;
    display: inline-block;
    vertical-align: middle;
    a{
      display: inline-block;
      background: $gray-lighter-1;
      color: $gray-dark;
      line-height: 20px;
      font-size: 15px;
      font-style: italic;
      padding: 14px  25px;
      transition: .3s all;

      &:hover{
        background: $gray-darker;
        color: $gray-light-3;
      }
    }
  }
}

.meta-list{
  font-style: italic;
  li{
    display: inline-block;
    vertical-align: middle;
    color: $gray-lighter;

    a{
      color: $primary;
      &:hover{
        text-decoration: underline;
      }
    }
    

    &:after{
      content: ',';

    }
    &:last-child{
      &:after{
        display: none;
      }
    }
  }
}

.list-inline-type-1{
  li{
    margin-top: 50px;
    margin-left: 20px;
    margin-right: 20px;
  }
}