/*
*
* Breadcrumbs
* --------------------------------------------------
*/

//== Breadcrumbs variables
//
//##

$bread-color: $primary;
$bread-active-color: $gray-light-4;

.section-breadcrumbs{
  border-bottom: 1px solid $gray-lighter-2;
}
.breadcrumb-wrapper{
  padding-top: 23px;
  padding-bottom:23px;
  
}

.breadcrumb-custom{
  margin-top: 20px;
  list-style: none;

  > li {
    display: inline-block;
    color: $bread-active-color;
    font-style: italic;

    > a{
      color: $bread-color;

      &:hover{
        text-decoration: underline;
      }
    }

    + li:before {
      padding: 0 10px;    
      content: "/";
      
    }
  }
}

.page-title{
  padding: 60px 0;
  @media (min-width: $screen-md-min) {
    padding: 170px 0 140px;
  }
  @media (min-width: $screen-lg-min){
    padding: 150px 0;
  }
  
  h5 + h1{
    margin-top: 0;
  }
  h5{
    font-weight: 600;
  }
}

.breadcrumb-custom-type-1{
  font-style: italic;
  text-align: left;
  > li {
    display: inline-block;
    color: $bread-color;

    > a{
      color: $bread-active-color;

      &:hover{
        text-decoration: underline;
        color: $bread-color;
      }
    }

    + li:before {
      padding: 0 10px;
      content: "/";
      color: $bread-active-color;
    }
  }
  & + *, & + .range{
    margin-top: 25px;
  }
}