/*
*
* Progress Bars
* --------------------------------------------------
*/



// Linear Progress Bars
// --------------------------------------------------

.progress-linear {
  position: relative;
  text-align: left;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;

  > div:first-child{
    width: calc(100% - 45px);
  }
  
  .progress-header{
    @include display-flex();
    @include align-items(center);
    @include justify-content(space-between);
    font-style: italic;
    color: #b8b8b6;
  }

  .progress-bar-linear-wrap, .progress-bar-linear {
    height: 100%;
    border-radius: 5px;
  }

  .progress-bar-linear-wrap {
    margin-top: 10px;
   background: $gray-lighter-2;
    height: 10px;
  }

  .progress-bar-linear {
    background-color: $primary;
    width: 0;
    transition: .5s all ease-in-out;
  }

  .progress-value{
    font-size: 18px;
    font-weight: 600;
    color: #b8b8b6;
    &:after{
      content:"%";
    }
  }
}

.progress-linear  + .progress-linear {
  margin-top: 15px;
}
