/*
*
* Icons
* --------------------------------------------------
*/


// Base styles
// --------------------------------------------------

.icon {
  display: inline-block;
  font-size: 16px;
  line-height: 1;

  &:before {
    position: relative;
    display: inline-block;
    font-weight: 400;
    font-style: normal;
    speak: none;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}


// Alternate icons
// --------------------------------------------------

.icon-default {

}

.icon-primary {
  color: $primary;
}


// Icon Shapes
// --------------------------------------------------

.icon-circle{
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  &:before{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
  }
  
  &-primary{
    color: $white;
    background: $primary;
  }
}


// Icon Sizes
// --------------------------------------------------

.icon-xs {

}

.icon-sm {

}

.icon-lg {
  font-size: 44px;
}

// Soc Icon
// ----------------------------------------------------

.soc-icon{
  > *{
    display: inline-block;
    vertical-align: middle;
    margin-left: -1px;
    margin-right: -1px;
  }
  .icon{
    position: relative;
    width: 48px;
    height: 48px;
    font-size: 20px;
    color: $white;
    background: $gray-dark;
    border-radius: 50%;
    margin-right: 1px;
    margin-left: 1px;
    transition:  250ms all ease-in-out;
    
    &:before{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    &:hover{
      background: $primary;
    }
  }
}