/*
*
* Circle Progress Bars
* --------------------------------------------------
*/


.progress-bar-circle {
  display: inline-block;
  position: relative;
  text-align: center;
  line-height: 1.2;

  canvas {
    vertical-align: middle;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    font-weight: 700;
    color: $gray-darker;   
    font-size: 46px;
    line-height: 46px;
    transform: translate(-50%, -50%);

    &:after {
      content: "%";
    }
  }
  
}
.progress-bar-circle-title{
  font-style: italic;
  padding-top: 15px;
  color: #5d6062;
}