/*
*
* Form styles
* --------------------------------------------------
*/

//== Forms variables
//

$input-height: 50px;

$form-wrap-offset: 15px;

$textarea-default-height:       135px;
$textarea-default-min-height:   $textarea-default-height * 0.8;
$textarea-default-max-height:   $textarea-default-height * 1.7;

$input-color: $gray-light-8;
$input-color-placeholder: $input-color;

$input-bg: $white;

$input-border: $gray-lighter-3;
$input-border-radius: 25px;

$input-font-size: 14px;
$input-line-height: 22px;
$input-font-weight: 400;

$input-padding-vertical: 12px;
$input-padding-horizontal: 15px;

$error-color: #d9534f;
$success-color: #5cb85c;



.rd-mailform {
  position: relative;
  text-align: left;
}

html .rd-mailform-inline{
  @media (min-width: $screen-xs-min){
    @include display-flex();

    .form-wrap{
      @include flex-grow(1);
    }

    .button{
      margin-top: 0;
      margin-left: 10px;
      flex: none;
    }
  }
 
}

.recaptcha{
  transform: scale(0.9);
  transform-origin: left center;
}


// Form input
// -------------------------

.form-input { 
  display: block;
  width: 100%;
  text-align: left;
  height: $input-height;
  padding: $input-padding-vertical $input-padding-horizontal;
  font-size: $input-font-size;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color;
  background-color: $input-bg;
  background-image: none;
  border: 1px solid $input-border;
  border-radius: $input-border-radius;
  -webkit-appearance: none;
  font-style: italic;

  &:focus {
    outline: 0;
  }

  &:-webkit-autofill ~ label,
  &:-webkit-autofill ~ .form-validation {
    color: $gray-base !important;
  }
}

textarea.form-input {
  height: $textarea-default-height;
  min-height: $textarea-default-min-height;
  max-height: $textarea-default-max-height;
  resize: vertical;
}

// Form wrapper
// -------------------------

.form-wrap {
  position: relative;
}

.form-wrap + .form-wrap{
  margin-top: $form-wrap-offset;
}

* + .form-button,
.form-wrap + .button{
  margin-top: 30px;
}
.sidebar-form-block, footer{
  .form-wrap + .button{
    margin-top: 10px;
    @media (min-width: $screen-xs-min){
      margin-top: 0;
    }
  }
}

.rd-navbar{
  .form-input{
    border-radius: 0;
  }
}

footer{
  .form-input{
    border-radius: 0;
  }
  .rd-mailform{
    .button{
      min-width: inherit;
      border-radius: 0;
      @media (min-width: $screen-lg-min) and (max-width: $screen-lg-min + 200px){
        padding: 13px 10px;
        margin-left: 0;
      }
    }
  }
}

// Form labels
// -------------------------

.form-label {
  position: absolute;
  top: $input-height / 2;
  left: $input-padding-horizontal;

  font-size: $input-font-size;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color-placeholder;
  pointer-events: none;
  z-index: 9;
  transition: .3s;
  transform: translateY(-50%);

  &.focus {
    opacity: 0;
  }

  &.auto-fill {
    color: $input-color;
  }
}

.form-label-outside {
  font-style: italic;
  @media (min-width: $screen-sm-min) {
    position: static;
    &, &.focus, &.auto-fill {
      opacity: 1;
      transform: none;
      color: $input-color-placeholder;
      font-size: $input-font-size;
    }

    & + .form-input + .form-validation{
      top: 32px;
    }
  }
}



// Form validation
// -------------------------

.form-validation {
  position: absolute;
  right: 20px;
  top: 1px;
  font-size: 11px;
  line-height: 11px;
  color: $error-color;
  margin-top: 3px;
  transition: .3s;
  z-index: 11;
 
}

.form-validation-left {
  .form-validation {
    right: auto;
    top: 100%;
    left: 0;
  }
}
.recaptcha-block{
  .form-validation{
    right: inherit;
    left: 5px;
  }
  
}

// Form output
// -------------------------

#form-output-global {
  position: fixed;
  bottom: 0;
  visibility: hidden;
  transform: translateY(calc(100% + 30px));
  transition: .3s all ease;
  z-index: 9999999;
  margin: 15px;

  &.active {
    transform: translateY(0);
    visibility: visible;
  }

  @media (min-width: $screen-sm) {
    margin: 30px;
  }
}

.form-output {
  position: absolute;
  top: 100%;
  left: 0;
  font-size: 14px;
  line-height: 1.5;
  margin-top: 2px;
  transition: .3s;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  &.error {
    color: $error-color;
  }

  &.success {
    color: $success-color;
  }
}


// Radio and Checkbox Custom
// -------------------------


// Base Styles
//

.radio .radio-custom,
.radio-inline .radio-custom,
.checkbox .checkbox-custom,
.checkbox-inline .checkbox-custom {
  opacity: 0;

  &, &-dummy {
    position: absolute;
    width: 14px;
    height: 14px;
    margin-left: -20px;
    margin-top: 5px;
    outline: none;
    cursor: pointer;
  }

  &-dummy {
    pointer-events: none;
    background: $gray-lighter;
    box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.15);

    &:after {
      position: absolute;
      opacity: 0;
    }
  }

  &:focus {
    outline: none;
  }
}

.radio-custom:checked + .radio-custom-dummy:after,
.checkbox-custom:checked + .checkbox-custom-dummy:after {
  opacity: 1;
}


// Custom Radio Styles
//

.radio,
.radio-inline {
  .radio-custom-dummy {
    border-radius: 50%;

    &:after {
      content: '';
      top: 3px;
      right: 3px;
      bottom: 3px;
      left: 3px;
      background: $gray-darker;
      border-radius: 50%;
    }
  }
}


// Custom Checkbox Styles
//

.checkbox,
.checkbox-inline {
  padding-left: 20px;
  .checkbox-custom-dummy {
    pointer-events: none;
    border-radius: 3px;
    margin-left: 0;
    left: 0;

    &:after {
      content: '\f222';
      font-family: "Material Design Icons";
      font-size: 20px;
      line-height: 10px;
      position: absolute;
      top: 0;
      left: 0;
      color: $gray-darker;
    }
  }
}

.sidebar-form-block{
  text-align: left;
  padding: 60px 30px;
  background: $gray-lighter-1;
  h3{
    line-height: 46px;
  }
  .form-label{
    font-size: 15px;
    font-style: italic;
    color: $gray-light-6;
  }
  .form-input{
    border: 1px solid $gray-light-7;
    border-radius: 0;
  }
  * + .section-subscribe{
    margin-top: 30px;
  }
  .section-subscribe{
    padding-top: 25px;
    border-top:  1px solid $gray-light-7;
    * + .soc-icon{
      margin-top: 25px;
    }
  }
  .button{
    min-width: inherit;
    border-radius: 0;
  }
}

.contact-box{
  * + .rd-mailform{
    margin-top: 30px;
  }
  .rd-mailform{
    max-width:  680px;
    .form-wrap + .form-wrap{
      margin-top: 10px;
    }
    .form-label{
      font-size: 15px;  
      font-style: italic;
      color: $gray-light-3;
    }
    .form-input{
      border: 1px solid $gray-lighter-3;
      padding: 10px 25px;
      border-radius: 30px;
    }
    textarea{
      height: 200px;
      @media (min-width: $screen-sm-min) {
        height: 280px;   
      }
    }
    .button{
      width: 200px;
    }
  }
}

.form-default{
  .form-wrap + .form-wrap{
    margin-top: 15px;
  }
  .button{
    border-radius: $input-border-radius;
  }
  * + .form-button,
  .form-wrap + .button{
    margin-top: 45px;
  }
  &.text-left{
    .form-label{
      padding-left: 20px;
    }
  }
}