/*
*
* Tabs
* --------------------------------------------------
*/


//== Tabs variables
//
//##

// Base
$tabs-color: $primary;
$tabs-background: $primary;
$tabs-font: 400 15px/24px $font-family-base;
$tabs-padding: 4px 8px;

// Horizontal tabs
$tabs-horizontal-active-color: $white;
$tabs-horizontal-active-background: $primary;
$tabs-horizontal-background: transparent;

// Vertical tabs
$tabs-vertical-active-color: $gray-dark;
$tabs-vertical-active-line: $primary;



// Base styles
//--------------------------------------

.tabs-custom {
  .nav-custom {
    &> li, &> li > a {
      display: block;
      position: relative;
    }
  }

  .nav-custom-tabs {
    font-size: 0;
    line-height: 0;

    li {
      margin: -1px 0;

      &+ li {
        margin-top: 7px;

        @media (min-width: $screen-sm-min) {
          margin-top: 0;
          margin-left: 9px;
        }
      }
    }
  }
}

.tabs-custom {
  .nav-custom-tabs {
    li a {
      position: relative;
      padding: $tabs-padding;
      font: $tabs-font;
      color: $tabs-color;
      border: 2px solid;
      text-align: center;
      vertical-align: middle;
      transition: .3s all ease;
    }

    li a:hover,
    li.active a {
      color: $white;
      background: $tabs-background;
      border-color: $tabs-background;
    }
  }
}

.tab-content {
  padding-top: 20px;

  @media (min-width: $screen-sm-min) {
    padding-top: 32px;
  }
}


// Horizontal tabs
//--------------------------------------

.tabs-custom {
  &.tabs-horizontal {
    @media (min-width: $screen-sm-min) {
      .nav-custom-tabs {
        position: relative;
        @include justify-content(center);
        @include display-flex;

        li a {
          display: block;
          padding: 10px 25px;
          font-weight: 700;
          color: #888a8c;
          letter-spacing: -.02em;
          background: $tabs-horizontal-background;
          border: 2px solid #b2b2b2;
          border-radius: 25px;
        }

        li a:hover,
        li.active a {
          color: $tabs-horizontal-active-color;
          background: $tabs-horizontal-active-background;
          border-color: $tabs-horizontal-active-background;

          &:before {
            transform: translate(-50%, 0);
            opacity: 1;
          }
        }
      }
    }
  }
}


// Vertical tabs
//--------------------------------------

.tabs-custom {
  &.tabs-vertical {
    @media (min-width: $screen-sm-min) {
      @include display-flex;
      @include flex-direction(row);
      @include flex-wrap(nowrap);
      @include align-items(stretch);

      .nav-custom-tabs {
        @include display-flex;
        @include flex-direction(column);
        @include flex-wrap(nowrap);
        @include align-items(stretch);
        @include flex-shrink(0);
        max-width: 50%;
      }

      .tab-content {
        @include flex-grow(1);
      }

      .nav-custom-tabs {
        text-align: left;
       

        li + li {
          margin-top: 12px;
          margin-left: 0;
        }

        li a {
          display: block;
          padding: 10px 25px;
          font-weight: 700;
          text-align: left;
          border: 2px solid transparent;
          background: transparent;
          border-radius: 25px;
          letter-spacing: -.02em;
          color: #888a8c;
        }

        li a:hover,
        li.active a {
          color: $tabs-horizontal-active-color;
          background: $tabs-horizontal-active-background;
          border-color: $tabs-horizontal-active-background;
        }
      }

      .tab-content {
        padding: 0 0 0 33px;
      }
    }

    @media (min-width: $screen-lg-min) {
      .nav-custom-tabs {
        min-width: 170px;
      }
    }
  }
}

.page {
  .tabs-custom-wrap + .range {
    margin-top: 0;
  }
}

.tabs-custom-type-1.tabs-custom{
  .nav-custom-tabs{
    @media (min-width: $screen-sm-min) {
     border-bottom: 1px solid #e1e1e1;   
      justify-content: flex-start;
      padding: 0 15px;
    }
    li{
      margin-top: 0;
       margin-bottom: 0;
      a{
        color: $gray-dark;
        background: #f9f9f9;
        border: 1px solid #e1e1e1;
        font-weight: 600;
       
        margin-bottom: -1px;
        &:hover{
          color: $gray-dark;
          background: $white;
          border: 1px solid #e1e1e1;
         
        }
        @media (min-width: $screen-sm-min) {
          font-size: 12px;
          color: $gray-dark;
          background: $gray-lighter-2;
          border: 1px solid #e1e1e1;
          border-radius: 0;
          &:hover{
            border-bottom: 1px solid $white;
          }
        }
        @media (min-width: $screen-sm-min) and (max-width: $screen-lg-min - 1px) {
          padding: 10px 15px;
        }
        @media (min-width: $screen-lg-min){
          font-size: 15px;
          padding: 10px 20px;
        }
      }
      &.active{
        a{
          color: $gray-dark;
          background: $white;
          border: 1px solid #e1e1e1;
          @media (min-width: $screen-sm-min) {
            border-bottom: 1px solid $white;
          }
        }
        
      }
    }
  }
}
.tab-content.tab-content-type-1{
  padding-top: 20px;
}